<template>
  <router-view/>
</template>

<script>

export default {
  data: () =>({
    isLoginSuccess: null
  }),
  computed: {
    isLogin(){
      this.isLoginSuccess = this.$store.getters.isLogin;
    }
  },
  mounted() {
    if(this.isLoginSuccess === true){
      this.$router.push('/List').catch(err => err);
    }else{
      this.$router.push('/Home').catch(err => err);
    }
  },
  beforeDestroy()
  {
    this.$socket.disconnect();
  }
}

  
</script>

