import Vue from 'vue'
import Router from 'vue-router'
import store from '../plugins/store.js'

import Root from '@/page/Root'
import Home from '@/page/Home'
import Document from '@/page/Document'
import Contact from '@/page/Contact'
import List from '@/page/List'
import Main from '@/page/Main'

import install from '@/page/Document/1_install_plugin'
import installWeb from '@/page/Document/1_install_web'
import server from '@/page/Document/2_server'
import binding from '@/page/Document/3_binding'
import bindingIocacher from '@/page/Document/3_binding_iocatcher'
import publish from '@/page/Document/4_publish'
import FAQ from '@/page/Document/5_FAQ'

Vue.use(Router)

const requireAuth = () => (from, to, next) => {
  const isAuthenticated = store.getters.isLogin;
  if(isAuthenticated) return next();
  next('/');
};

export default new Router({
  base: __dirname,
  mode: "history",
  // 다른페이지 이동시 상단으로 이동시켜주는 func
  scrollBehavior: function(to) {
    if (to.history) {
      return { selector: to.history }
    } else {
      return { x: 0, y: 0 }
    }
  },
  routes: [
    {
      path: "/",
      component: Root,
      name: "Root",
      children:[
        {
          path: "/Home",
          component: Home,
          name: "Home"
        },
        {
          path: "/Document",
          component: Document,
          name: "Document",
          children:[
            {
              path: "/Document/install",
              component: install,
              name: "install"
            },
            {
              path: "/Document/installWeb",
              component: installWeb,
              name: "installWeb"
            },
            {
              path: "/Document/server",
              component: server,
              name: "server"
            },
            {
              path: "/Document/binding",
              component: binding,
              name: "binding"
            },
            {
              path: "/Document/bindingIocacher",
              component: bindingIocacher,
              name: "bindingIocacher"
            },
            {
              path: "/Document/publish",
              component: publish,
              name: "publish"
            },
            {
              path: "/Document/FAQ",
              component: FAQ,
              name: "FAQ"
            },
          ]
        },
        {
          path: "/Contact",
          component: Contact,
          name: "Contact"
        },
        {
          path: "/List",
          component: List,
          name: "List"
        }
      ]
    },
    {
      path: "/Main",
      component: Main,
      name: "Main"
    }
  ]
})
