// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import App from './App';
import router from './router';
import VueKonva from 'vue-konva';
import socketAPI from '../../common/socketAPI.json';
import Vuetify from 'vuetify';
import BootstrapVue from 'bootstrap-vue';
import setting from'../../common/setting.json';
import VueGoodTablePlugin from 'vue-good-table';
import store from './plugins/store.js';
import AnimateCss from 'animate.css'
import Scrollspy from 'vue2-scrollspy';
import VueHighlightJS from 'vue-highlight.js';

// component
import './components';

Vue.use(VueHighlightJS, {
	// Register only languages that you want
	languages: {
		css,
		javascript,
    json,
    xml,
    vue
	}
});
// Highlight.js 사용방법
// <highlight-code class="code" lang="javascript"> code 내용 </highlight-code>

// vue2-scrollspy
Vue.use(Scrollspy);

// c3 style
import 'c3/c3.min.css';
import 'c3/c3.min.js';

// vuetify
import 'vuetify/dist/vuetify.min.css'
Vue.use(Vuetify, {
  theme: {
    primary: '#b7472a',
    secondary: '#ede857',
    startbtn: '#91d135',
    text: '#565656',
    bgcolor:'#f0f0f0'
  }
})

// Animatie.css url-https://github.com/daneden/animate.css
Vue.use(AnimateCss);

// bootstrap
Vue.use(BootstrapVue)
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'

Vue.config.productionTip = false

// VueGoodTable
import 'vue-good-table/dist/vue-good-table.css'

Vue.use(VueGoodTablePlugin);

// konva
Vue.use(VueKonva)

// VueEasyTable
import './extLib/easytable/themes-base/index.css';
import {VPagination, VTable } from './extLib/easytable';

Vue.component("DPagination", VPagination);
Vue.component("DTable", VTable);

// Highlight.js languages (Only required languages)
import css from 'highlight.js/lib/languages/css';
import javascript from 'highlight.js/lib/languages/javascript';
import json from 'highlight.js/lib/languages/json';
import xml from 'highlight.js/lib/languages/xml';
import vue from 'vue-highlight.js/lib/languages/vue';

/*
* Import Highlight.js theme
* Find more: https://highlightjs.org/static/demo/
* Find more: https://github.com/gluons/vue-highlight.js/tree/master
*/
import 'highlight.js/styles/vs2015.css';

// setting prototype
Vue.prototype.$setting = setting;

// socket api const 정의
// this.$socketAPI.GetMasterData 처럼 사용가능
Vue.prototype.$socketAPI = socketAPI;

// 각 components 에서 this.$eventBus.$emit('key', 'value'); 이벤트 발행
// this.$eventBus.$on('key', 'functionName'); 이벤트 구독, created or mounted 때 등록
// this.$eventBus.$off('key'); 이벤트 구독해제, beforeDestory 때 등록
Vue.prototype.$eventBus = new Vue();

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})
