import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        msalConfig: {
            auth: {
              clientId: '72144ab0-5110-41e5-bea2-f325e65dd4ad',
              authority: "https://login.microsoftonline.com/common"
            },
            cache: {
              cacheLocation: "localStorage",
              storeAuthStateInCookie: true
            }
        },
        requestObj: {
            scopes: ["files.read"]
        },
        graphConfig: {
            graphMeEndpoint: "https://graph.microsoft.com/v1.0/me"
        },
        accessToken: null,
        downloadData: {},
        myMSALObj:{},
        drawerDisplay: false
    },
    getters: {
        isLogin: function(state){
            return (state.accessToken == null ) ? false : true;
        },
        getDrawerDisplay: function(state){
            return state.drawerDisplay;
        }
    },
    actions:{
    },
    mutations: {
        setToken: function (state, value){
            state.accessToken = value;
        // console.log("vuex token",state.accessToken);
        },
        setData: function (state, value){
            state.downloadData = value;
        // console.log("vuex data",state.DownloadData);
        },
        setmyMSALOBj: function (state, value){
            state.myMSALObj = value;
        // console.log("vuex data",state.myMSALObj);
        }
    }
});