<template>
	<div>
		<DTable
			:height="height"
			:style="width"
			:title-rows="titleRows"
			:columns="columns"
			:table-data="tableData"
			:even-bg-color="evenBgColor"
			:row-hover-color="rowHoverColor"
			:row-click-color="rowClickColor"
			:multiple-sort="false"
			@sort-change="sortChange"
		></DTable>
	</div>
</template>

<script>
import $ from "jquery";

export default{
	data:() => ({
	}),
	props: [
		'columns',
		'titleRows',
		'height',
		'tableData',
		'width',
		'evenBgColor',
		'rowHoverColor',
		'rowClickColor'
	],
	created() {
	},
	mounted() {
	},
	methods: {
		sortChange(params){
			var sortKey = Object.keys(params);
			// sortKey만큼 반복
			for(let i = 0; i < sortKey.length; i++){
				var sortId = sortKey[i];
				var tableDataColumn = this.tableData[0];
				var sortTableData = tableDataColumn[sortId];
				
				// 숫자인지 문자인지 확인
				var sortIsnan = isNaN(sortTableData);

				// 숫자일 경우
				if(sortIsnan === false){
					if (sortId.length > 0){
						this.tableData.sort(function (a, b) {
							// asc 오름차순 desc 내림차순 
							if (params[sortId] === 'asc'){
								return a[sortId] - b[sortId];
							}if(params[sortId] === 'desc'){
								return b[sortId] - a[sortId];
							}else{
								return 0;
							}
						});
					}
				}
				// 문자일 경우
				if(sortIsnan === true){
					if (sortId.length > 0){
						this.tableData.sort(function (a, b) {
							if (params[sortId] === 'asc'){
								return a[sortId] < b[sortId] ? -1 : a[sortId] > b[sortId] ? 1 : 0;
							}if(params[sortId] === 'desc'){
								return a[sortId] > b[sortId] ? -1 : a[sortId] < b[sortId] ? 1 : 0;
							}else{
								return 0;
							}
						});
					}
				}
			}
        },
	},
	watch: {
	},
}
</script>
